.item{
    width: 200px;
    height:250px;
    border: 1px solid #ccc;
}
.item .img{
    width: 100%;
    height: 150px;
}
.item .img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.details{
    padding: 5px;
}
.details h3{
    margin-bottom: 10px;
}
.details p{
   
     font-weight: 500;
     color:#888
}
.details button{
    margin-top: 5px;
    padding: 5px;
    background-color: teal;
    color: white;
    border: none;
    border-radius: 3px;
}
.details button:hover{
    background-color: rgb(6, 83, 83);
}
.btnRemove{
    background-color: rgb(207, 10, 10) !important;
}