.cart-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.cart-product{
    display: flex;
    gap: 5px;
    align-items: center;
    margin-bottom: 10px;
    border: 1px solid grey;
}
.cart-product .img{
    width: 100px;
    height: 100px;
}
.cart-product .img img{
    width: 100%;
    height: 100%;
}
.cart-name, 
.cart-amt{
    font-size: 20px;
    font-weight: 600;
}
.payment{
    background-color: lightblue;
    border: none;
    padding: 5px;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    text-decoration: none;
}