/*destop view*/

.home{
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    flex-direction: column;
}
.headerContainer{
    width: auto;
    margin-left: 30px;
}
.home .headerContainer h1{
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 70px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: black;
}
.home .headerContainer p{
    margin-top: 15px;
    padding: 0;
    font-size: 40px;
    font-weight: lighter;
    color: black;
}
.home .headerContainer button{
    background-color: black;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    height: 50px;
    width: 180px;
    text-decoration: none;
    color: white;
    font-size: 20px;
}
.home .headerContainer button:hover{
    background-color: rgb(61, 50, 26);
    cursor: pointer;
    transition: 0.3s ease-in;
}
/*Mobile view*/
@media only screen and (max-width:600px){
    .home{
        justify-content: center;
        align-items: center;
    }
    .headerContainer{
    background-color: rgb(61, 50, 26);
    margin-left:0;
    border-radius: 10px;
    width: 90%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   }
   .home .headerContainer h1{
    font-size: 30px;
    margin-top: 20px;
    height: 30px;
    margin: 30px 0px;
    color:white;
   }
   .home .headerContainer p{
    font-size: 20px;
    color: white;
   }
   .home .headerContainer button{
    background-color: white;
    color:rgb(61, 50, 26);
    margin-bottom: 30px;
   }
}